import React from 'react';

import { CityContext } from '../../context/CityContext';
import MetaData from '../../components/meta-data/MetaData';
import HeaderPage from '../../components/header-page/HeaderPage';
import FooterPage from '../../components/footer-page/FooterPage';

import Faq from '../../components/faq/Faq';
import { ThemeCityContext } from '../../context/ThemeCityContext';
import { ROUTES } from '../../utils/routes';
import { ClubsCityContext } from '../../context/ClubsCityContext';
import { getDataCity } from '../../utils';

const YarHome = ({ data }: any) => {
  const PAGE_CITY = ROUTES.YAR.NAME;
  const DATA_CITY = getDataCity(data, PAGE_CITY);
  const clubs =
    !!DATA_CITY &&
    [...DATA_CITY.clubs].map(el => {
      return {
        name: el.name,
        address: el.address,
      };
    });
  return (
    <ThemeCityContext.Provider value="">
      <CityContext.Provider value={PAGE_CITY}>
        <MetaData
          title="Fitness House в Ярославле | Безлимитный абонемент за месяц 1500 руб."
          description="Фитнес-клуб с оплатой за месяц 1500 руб. Спа, групповые занятия, тренажерный зал."
        />

        <HeaderPage />

        <main className="min-h-screen bg-white-200"></main>

        <div className="overflow-hidden">
          <Faq title="отвечаем на Вопросы" />
          <ClubsCityContext.Provider value={clubs}>
            <FooterPage />
          </ClubsCityContext.Provider>
        </div>
      </CityContext.Provider>
    </ThemeCityContext.Provider>
  );
};

export default YarHome;
