import React from 'react';

import { ROUTES } from '../../utils/routes';
import { getDataCity } from '../../utils';
import { CityContext } from '../../context/CityContext';
import { ThemeCityContext } from '../../context/ThemeCityContext';
import { ClubsCityContext } from '../../context/ClubsCityContext';
import { SaleTimerCityContext } from '../../context/SaleTimerCityContext';
import MetaData from '../../components/meta-data/MetaData';
import HeaderPage from '../../components/header-page/HeaderPage';
import FooterPage from '../../components/footer-page/FooterPage';

import Promo from '../../components/promo/Promo';
import Timer from '../../components/timer/Timer';
import ClubServices from '../../components/club-services/ClubServices';
import Advantages from '../../components/advantages/Advantages';
import Tariffs from '../../components/tariffs/Tariffs';
import ClubsGallery from '../../components/clubs-gallery/ClubsGallery';
import Faq from '../../components/faq/Faq';

const SpbHome = ({ data }: any) => {
  const PAGE_CITY = ROUTES.SPB.NAME;
  const DATA_CITY = getDataCity(data, PAGE_CITY);
  const clubs =
    !!DATA_CITY &&
    [...DATA_CITY.clubs].map(el => {
      return {
        name: el.name,
        address: el.address,
      };
    });
  return (
    <ThemeCityContext.Provider value="">
      <CityContext.Provider value={PAGE_CITY}>
        <MetaData
          title="Fitness House в Санкт-Петербурге | Безлимитный абонемент за месяц 1500 руб."
          description="Фитнес-клуб с оплатой за месяц 1500 руб. Спа, групповые занятия, тренажерный зал."
        />

        <HeaderPage />

        <main>
          <Promo />
          <SaleTimerCityContext.Provider
            value={{ startDate: '', endDate: 'December, 25, 2023' }}
          >
            <Timer title="До конца акции осталось:" />
          </SaleTimerCityContext.Provider>
          <ClubServices title="В абонемент включено" />
          <Tariffs title="Тарифы" count={2} />
          <div className="overflow-hidden">
            <Advantages title="Ваша выгода" />
          </div>
          <ClubsGallery title="клубы fitness house" />
        </main>

        <div className="overflow-hidden">
          <Faq title="отвечаем на Вопросы" />
          <ClubsCityContext.Provider value={clubs}>
            <FooterPage />
          </ClubsCityContext.Provider>
        </div>
      </CityContext.Provider>
    </ThemeCityContext.Provider>
  );
};

export default SpbHome;
