import axios from 'axios';
import { AppDispatch } from '../store';
import { IPrice } from '../../models/IPrice';
import { priceSlice } from './PriceSlice';

export const fetchPrice = () => async (dispatch: AppDispatch) => {
  try {
    dispatch(priceSlice.actions.priceFetching());
    const response = await axios.get<IPrice[]>('../data.json');
    dispatch(priceSlice.actions.priceFetchingSuccess(response.data));
  } catch (e: any) {
    dispatch(priceSlice.actions.priceFetchingError(e.message));
  }
};
