import { PayloadAction, createSlice } from '@reduxjs/toolkit';
import { IPrice } from '../../models/IPrice';

interface PriceState {
  price: IPrice[];
  isLoading: boolean;
  error: string;
  count: number;
}

const initialState = {
  price: [],
  isLoading: false,
  error: '',
  count: 0,
};

export const priceSlice = createSlice({
  name: 'price',
  initialState,
  reducers: {
    priceFetching(state) {
      state.isLoading = true;
    },
    // IPrice[]
    priceFetchingSuccess(state, action: PayloadAction<any>) {
      state.isLoading = false;
      state.error = '';
      state.price = action.payload.services;
    },
    priceFetchingError(state, action: PayloadAction<string>) {
      state.isLoading = false;
      state.error = action.payload;
    },
  },
});

export default priceSlice.reducer;
